export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-3\">\n        Auto width\n      </label>\n\n      <div class=\"col-sm-9\">\n        <MultipleSelect\n          single\n        >\n          <option value=\"1\">\n            First\n          </option>\n          <option value=\"2\">\n            Second\n          </option>\n          <option value=\"3\">\n            Third\n          </option>\n          <option value=\"4\">\n            Fourth\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-3\">\n        Data width\n      </label>\n\n      <div class=\"col-sm-9\">\n        <MultipleSelect\n          single\n          :data=\"data\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-3\">\n        Large width\n      </label>\n\n      <div class=\"col-sm-9\">\n        <MultipleSelect\n          single\n        >\n          <option value=\"1\">\n            This is the first option and value is 1\n          </option>\n          <option value=\"2\">\n            This is the second option and value is 2\n          </option>\n          <option value=\"3\">\n            This is the third option and value is 3\n          </option>\n          <option value=\"4\">\n            This is the fourth option and value is 4\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-3\">\n        Dropdown width\n      </label>\n\n      <div class=\"col-sm-9\">\n        <MultipleSelect\n          single\n          width=\"200\"\n        >\n          <option value=\"1\">\n            This is the first option and value is 1\n          </option>\n          <option value=\"2\">\n            This is the second option and value is 2\n          </option>\n          <option value=\"3\">\n            This is the third option and value is 3\n          </option>\n          <option value=\"4\">\n            This is the fourth option and value is 4\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-3\">\n        Group Select\n      </label>\n\n      <div class=\"col-sm-9\">\n        <MultipleSelect>\n          <optgroup label=\"Group 1\">\n            <option value=\"1\">\n              Option 1\n            </option>\n            <option value=\"2\">\n              Option 2\n            </option>\n            <option value=\"3\">\n              Option 3\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 2\">\n            <option value=\"4\">\n              Option 4\n            </option>\n            <option value=\"5\">\n              Option 5\n            </option>\n            <option value=\"6\">\n              Option 6\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 3\">\n            <option value=\"7\">\n              Option 7\n            </option>\n            <option value=\"8\">\n              Option 8\n            </option>\n            <option value=\"9\">\n              Option 9\n            </option>\n          </optgroup>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-3\">\n        Large Group\n      </label>\n\n      <div class=\"col-sm-9\">\n        <MultipleSelect>\n          <optgroup label=\"This is group 1\">\n            <option value=\"1\">\n              This is option 1\n            </option>\n            <option value=\"2\">\n              This is option 2\n            </option>\n            <option value=\"3\">\n              This is option 3\n            </option>\n          </optgroup>\n          <optgroup label=\"This is group 2\">\n            <option value=\"4\">\n              This is option 4\n            </option>\n            <option value=\"5\">\n              This is option 5\n            </option>\n            <option value=\"6\">\n              This is option 6\n            </option>\n          </optgroup>\n          <optgroup label=\"This is group 3\">\n            <option value=\"7\">\n              This is option 7\n            </option>\n            <option value=\"8\">\n              This is option 8\n            </option>\n            <option value=\"9\">\n              This is option 9\n            </option>\n          </optgroup>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'Auto Width',\n      desc: 'Auto width by default without setting width style.',\n      data: [\n        {\n          value: 1,\n          text: 'Options 1',\n          selected: true\n        },\n        {\n          value: 2,\n          text: 'Options 2'\n        },\n        {\n          value: 3,\n          text: 'Options 3'\n        }\n      ]\n    }\n  }\n}\n</script>\n"