export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Methods\n      </label>\n\n      <div class=\"col-sm-10\">\n        <button\n          class=\"btn btn-secondary\"\n          @click=\"getOptions\"\n        >\n          getOptions\n        </button>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"select\"\n        >\n          <option value=\"text1\">\n            text1\n          </option>\n          <option value=\"text2\">\n            text2\n          </option>\n          <option value=\"text3\">\n            text3\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The getOptions',\n      desc: 'Use <code>multipleSelect(\\'getOptions\\')</code> to get the options object.',\n      options: {\n        filter: true\n      }\n    }\n  },\n  methods: {\n    getOptions () {\n      alert(JSON.stringify(this.$refs.select.getOptions(), null, 4))\n      console.log(this.$refs.select.getOptions())\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"