export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Array\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          multiple\n          :data=\"basicData\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Object\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          multiple\n          :data=\"objectData\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        String Array\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          multiple\n          :data=\"stringData\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Number Array\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          multiple\n          :data=\"numberData\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Group Array\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          multiple\n          :data=\"groupData\"\n        />\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The Data',\n      desc: 'Use <code>data</code> as data source.',\n      basicData: [\n        {\n          text: 'January',\n          value: 1\n        },\n        {\n          text: 'February',\n          value: 2\n        },\n        {\n          text: 'March',\n          value: 3\n        },\n        {\n          text: 'April',\n          value: 4\n        },\n        {\n          text: 'May',\n          value: 5\n        },\n        {\n          text: 'June',\n          value: 6\n        },\n        {\n          text: 'July',\n          value: 7\n        },\n        {\n          text: 'August',\n          value: 8\n        },\n        {\n          text: 'September',\n          value: 9\n        },\n        {\n          text: 'October',\n          value: 10\n        },\n        {\n          text: 'November',\n          value: 11\n        },\n        {\n          text: 'December',\n          value: 12\n        }\n      ],\n      objectData: {\n        1: 'January',\n        2: 'February',\n        3: 'March',\n        4: 'April',\n        5: 'May',\n        6: 'June',\n        7: 'July',\n        8: 'August',\n        9: 'September',\n        10: 'October',\n        11: 'November',\n        12: 'December'\n      },\n      stringData: [\n        'January', 'February', 'March'\n      ],\n      numberData: [\n        1, 2, 3\n      ],\n      groupData: [\n        {\n          type: 'optgroup',\n          label: 'Group 1',\n          children: [\n            {\n              text: 'January',\n              value: 1,\n              selected: true\n            },\n            {\n              text: 'February',\n              value: 2,\n              disabled: true\n            },\n            {\n              text: 'March',\n              value: 3\n            },\n            {\n              text: 'April',\n              value: 4\n            },\n            {\n              text: 'May',\n              value: 5\n            },\n            {\n              text: 'June',\n              value: 6\n            }\n          ]\n        },\n        {\n          type: 'optgroup',\n          label: 'Group 2',\n          children: [\n            {\n              text: 'July',\n              value: 7\n            },\n            {\n              text: 'August',\n              value: 8\n            },\n            {\n              text: 'September',\n              value: 9\n            },\n            {\n              text: 'October',\n              value: 10\n            },\n            {\n              text: 'November',\n              value: 11\n            },\n            {\n              text: 'December',\n              value: 12\n            }\n          ]\n        }\n      ]\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"