export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-3 checkbox\">\n        <input\n          v-model=\"caseSensitive\"\n          type=\"checkbox\"\n        > Case Sensitive\n      </label>\n\n      <div class=\"col-sm-9\">\n        <MultipleSelect\n          :options=\"options\"\n        >\n          <option value=\"1\">\n            abc\n          </option>\n          <option value=\"2\">\n            bcd\n          </option>\n          <option value=\"3\">\n            cde\n          </option>\n          <option value=\"4\">\n            def\n          </option>\n          <option value=\"5\">\n            efg\n          </option>\n          <option value=\"6\">\n            fgh\n          </option>\n          <option value=\"7\">\n            ghi\n          </option>\n          <option value=\"8\">\n            hij\n          </option>\n          <option value=\"9\">\n            ijk\n          </option>\n          <option value=\"10\">\n            jkl\n          </option>\n          <option value=\"11\">\n            klm\n          </option>\n          <option value=\"12\">\n            lmn\n          </option>\n          <option value=\"13\">\n            mno\n          </option>\n          <option value=\"14\">\n            nop\n          </option>\n          <option value=\"15\">\n            opq\n          </option>\n          <option value=\"16\">\n            pqr\n          </option>\n          <option value=\"17\">\n            qrs\n          </option>\n          <option value=\"18\">\n            rst\n          </option>\n          <option value=\"19\">\n            stu\n          </option>\n          <option value=\"20\">\n            tuv\n          </option>\n          <option value=\"21\">\n            uvw\n          </option>\n          <option value=\"22\">\n            vwx\n          </option>\n          <option value=\"23\">\n            wxy\n          </option>\n          <option value=\"24\">\n            xyz\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'Custom Filter',\n      desc: 'Use <code>customFilter</code> to custom filter, in this case we filter by <code>startWith</code>.',\n      caseSensitive: false,\n      options: {\n        filter: true,\n        customFilter: (label, text, originalLabel, originalText) => {\n          if (this.caseSensitive) {\n            return originalLabel.indexOf(originalText) === 0\n          }\n          return label.indexOf(text) === 0\n        }\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"