export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Methods\n      </label>\n\n      <div class=\"col-sm-10\">\n        <button\n          class=\"btn btn-secondary\"\n          @click=\"setSelects\"\n        >\n          SetSelects\n        </button>\n        <button\n          class=\"btn btn-secondary\"\n          @click=\"getSelects\"\n        >\n          GetSelects\n        </button>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"select\"\n        >\n          <option value=\"1\">\n            January\n          </option>\n          <option value=\"2\">\n            February\n          </option>\n          <option value=\"3\">\n            March\n          </option>\n          <option value=\"4\">\n            April\n          </option>\n          <option value=\"5\">\n            May\n          </option>\n          <option value=\"6\">\n            June\n          </option>\n          <option value=\"7\">\n            July\n          </option>\n          <option value=\"8\">\n            August\n          </option>\n          <option value=\"9\">\n            September\n          </option>\n          <option value=\"10\">\n            October\n          </option>\n          <option value=\"11\">\n            November\n          </option>\n          <option value=\"12\">\n            December\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The setSelects/getSelects',\n      desc: 'Use <code>multipleSelect(\\'setSelects\\', [])</code> and <code>multipleSelect(\\'getSelects\\')</code> to set selects and get selects.'\n    }\n  },\n  methods: {\n    setSelects () {\n      this.$refs.select.setSelects([\n        1, 3\n      ])\n    },\n\n    getSelects () {\n      alert('Selected values: ' + this.$refs.select.getSelects())\n      alert('Selected texts: ' + this.$refs.select.getSelects('text'))\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"