export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Methods\n      </label>\n\n      <div class=\"col-sm-10\">\n        <button\n          class=\"btn btn-secondary\"\n          @click=\"check\"\n        >\n          Check\n        </button>\n        <button\n          class=\"btn btn-secondary\"\n          @click=\"uncheck\"\n        >\n          Uncheck\n        </button>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Single Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"single\"\n          single\n        >\n          <option value=\"1\">\n            Value 1\n          </option>\n          <option value=\"2\">\n            Value 2\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Multiple Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"multiple\"\n        >\n          <option\n            value=\"1\"\n            selected\n          >\n            Value 1\n          </option>\n          <option value=\"2\">\n            Value 2\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Group Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"group\"\n        >\n          <optgroup label=\"Group 1\">\n            <option\n              value=\"1\"\n              selected\n            >\n              Value 1\n            </option>\n            <option value=\"2\">\n              Value 2\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 2\">\n            <option\n              value=\"3\"\n              selected\n            >\n              Value 3\n            </option>\n            <option value=\"4\">\n              Value 4\n            </option>\n          </optgroup>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The check/uncheck',\n      desc: 'Methods: <code>multipleSelect(\\'check\\', 2)</code> and <code>multipleSelect(\\'uncheck\\', 2)</code> to check and uncheck a select option.'\n    }\n  },\n  methods: {\n    check () {\n      this.$refs.single.check(2)\n      this.$refs.multiple.check(2)\n      this.$refs.group.check(2)\n    },\n\n    uncheck () {\n      this.$refs.single.uncheck(2)\n      this.$refs.multiple.uncheck(2)\n      this.$refs.group.uncheck(2)\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"