export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Option text\n      </label>\n      <div class=\"col-sm-10\">\n        <input\n          v-model=\"input\"\n          class=\"form-control\"\n          type=\"text\"\n          required=\"required\"\n          placeholder=\"Enter text\"\n        >\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Option attribute\n      </label>\n      <div class=\"col-sm-10\">\n        <label class=\"checkbox\">\n          <input\n            v-model=\"selected\"\n            type=\"checkbox\"\n          > Selected\n        </label>\n        <label class=\"checkbox\">\n          <input\n            v-model=\"disabled\"\n            type=\"checkbox\"\n          > Disabled\n        </label>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\" />\n      <div class=\"col-sm-10\">\n        <button\n          class=\"btn btn-secondary\"\n          @click=\"refreshAdd\"\n        >\n          Add + Refresh\n        </button>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Data Select\n      </label>\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          :data=\"data\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Option Select\n      </label>\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"select\"\n        >\n          <option\n            v-for=\"item of data\"\n            :key=\"item.value\"\n            :value=\"item.value\"\n            :selected=\"item.selected\"\n            :disabled=\"item.disabled\"\n          >\n            {{ item.text }}\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The refresh',\n      desc: 'Use <code>multiple(\\'refresh\\')</code> to refresh/reload the Multiple Select. If you\\'re dynamically adding/removing option tags on the original select via AJAX or DOM manipulation methods, call refresh to reflect the changes.',\n      input: '',\n      selected: false,\n      disabled: false,\n      data: [\n        {\n          value: 'text1',\n          text: 'text1'\n        },\n        {\n          value: 'text2',\n          text: 'text2'\n        },\n        {\n          value: 'text3',\n          text: 'text3'\n        }\n      ]\n    }\n  },\n  methods: {\n    refreshAdd () {\n      this.data.push({\n        value: this.input,\n        text: this.input,\n        selected: this.selected,\n        disabled: this.disabled\n      })\n\n      // use <option> need call refresh method\n      this.$nextTick(() => {\n        this.$refs.select.refresh()\n      })\n      this.input = ''\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"