export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Locale Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          v-model=\"options.locale\"\n          single\n        >\n          <option value=\"en-US\">\n            English\n          </option>\n          <option value=\"es-ES\">\n            Spanish\n          </option>\n          <option value=\"zh-CN\">\n            \n          </option>\n          <option value=\"zh-TW\">\n            \n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Result Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          :options=\"options\"\n        >\n          <option value=\"1\">\n            January\n          </option>\n          <option value=\"2\">\n            February\n          </option>\n          <option value=\"3\">\n            March\n          </option>\n          <option value=\"4\">\n            April\n          </option>\n          <option value=\"5\">\n            May\n          </option>\n          <option value=\"6\">\n            June\n          </option>\n          <option value=\"7\">\n            July\n          </option>\n          <option value=\"8\">\n            August\n          </option>\n          <option value=\"9\">\n            September\n          </option>\n          <option value=\"10\">\n            October\n          </option>\n          <option value=\"11\">\n            November\n          </option>\n          <option value=\"12\">\n            December\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n// import 'multiple-select/dist/multiple-select-locale-all'\n\nexport default {\n  data () {\n    return {\n      title: 'The Locale',\n      desc: 'Use <code>locale: true</code> to set the locale.',\n      options: {\n        locale: 'en-US'\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"