export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Methods\n      </label>\n\n      <div class=\"col-sm-10\">\n        <button\n          class=\"btn btn-danger\"\n          @click=\"destroy\"\n        >\n          Destroy\n        </button>\n        <button\n          class=\"btn btn-primary\"\n          @click=\"build\"\n        >\n          Build\n        </button>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"select\"\n        >\n          <option value=\"1\">\n            January\n          </option>\n          <option value=\"2\">\n            February\n          </option>\n          <option value=\"3\">\n            March\n          </option>\n          <option value=\"4\">\n            April\n          </option>\n          <option value=\"5\">\n            May\n          </option>\n          <option value=\"6\">\n            June\n          </option>\n          <option value=\"7\">\n            July\n          </option>\n          <option value=\"8\">\n            August\n          </option>\n          <option value=\"9\">\n            September\n          </option>\n          <option value=\"10\">\n            October\n          </option>\n          <option value=\"11\">\n            November\n          </option>\n          <option value=\"12\">\n            December\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The destroy',\n      desc: 'Use <code>multiple(destroy\\')</code> to destroy the select.'\n    }\n  },\n  methods: {\n    destroy () {\n      this.$refs.select.destroy()\n    },\n\n    build () {\n      this.$refs.select._initSelect()\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"