export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Events\n      </label>\n\n      <div class=\"col-sm-10\">\n        <textarea\n          ref=\"log\"\n          v-model=\"log\"\n          class=\"form-control\"\n          readonly\n          rows=\"10\"\n        />\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Group Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          :options=\"options\"\n          @on-open=\"onOpen\"\n          @on-close=\"onClose\"\n          @on-check-all=\"onCheckAll\"\n          @on-uncheck-all=\"onUncheckAll\"\n          @on-focus=\"onFocus\"\n          @on-blur=\"onBlur\"\n          @on-optgroup-click=\"onOptgroupClick\"\n          @on-click=\"onClick\"\n          @on-filter=\"onFilter\"\n          @on-after-create=\"onAfterCreate\"\n        >\n          <optgroup label=\"Group 1\">\n            <option value=\"1\">\n              Option 1\n            </option>\n            <option value=\"2\">\n              Option 2\n            </option>\n            <option value=\"3\">\n              Option 3\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 2\">\n            <option value=\"4\">\n              Option 4\n            </option>\n            <option value=\"5\">\n              Option 5\n            </option>\n            <option value=\"6\">\n              Option 6\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 3\">\n            <option value=\"7\">\n              Option 7\n            </option>\n            <option value=\"8\">\n              Option 8\n            </option>\n            <option value=\"9\">\n              Option 9\n            </option>\n          </optgroup>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The Events',\n      desc: 'Multiple Select has some callback events.',\n      options: {\n        filter: true\n      },\n      log: ''\n    }\n  },\n  watch: {\n    log () {\n      const log = this.$refs.log\n      log.scrollTop = log.scrollHeight - log.style.height\n    }\n  },\n  methods: {\n    onOpen () {\n      this.log += 'onOpen event fire!\\n'\n    },\n    onClose () {\n      this.log += 'onClose event fire!\\n'\n    },\n    onCheckAll () {\n      this.log += 'onCheckAll event fire!\\n'\n    },\n    onUncheckAll () {\n      this.log += 'onUncheckAll event fire!\\n'\n    },\n    onFocus () {\n      this.log += 'onFocus event fire!\\n'\n    },\n    onBlur () {\n      this.log += 'onBlur event fire!\\n'\n    },\n    onOptgroupClick (view) {\n      this.log += 'onOptgroupClick event fire! view: ' + JSON.stringify(view) + '\\n'\n    },\n    onClick (view) {\n      this.log += 'onClick event fire! view: ' + JSON.stringify(view) + '\\n'\n    },\n    onFilter (text) {\n      this.log += 'onFilter event fire! text: ' + text + '\\n'\n    },\n    onAfterCreate () {\n      this.log += 'onAfterCreate event fire!\\n'\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"