export default "<template>\n  <div>\n    <form @submit.prevent=\"submit\">\n      <div class=\"form-group row\">\n        <label class=\"col-sm-2\">\n          Single Select\n        </label>\n\n        <div class=\"col-sm-10\">\n          <MultipleSelect\n            name=\"select1\"\n            single\n          >\n            <option value=\"1\">\n              First\n            </option>\n            <option value=\"2\">\n              Second\n            </option>\n            <option value=\"3\">\n              Third\n            </option>\n            <option value=\"4\">\n              Fourth\n            </option>\n          </MultipleSelect>\n        </div>\n      </div>\n\n      <div class=\"form-group row\">\n        <label class=\"col-sm-2\">\n          Multiple Select\n        </label>\n\n        <div class=\"col-sm-10\">\n          <MultipleSelect\n            name=\"select2\"\n            required\n          >\n            <option value=\"1\">\n              First\n            </option>\n            <option value=\"2\">\n              Second\n            </option>\n            <option value=\"3\">\n              Third\n            </option>\n            <option value=\"4\">\n              Fourth\n            </option>\n          </MultipleSelect>\n        </div>\n      </div>\n\n      <div class=\"form-group row\">\n        <div class=\"col-sm-10 offset-sm-2\">\n          <button\n            type=\"submit\"\n            class=\"btn btn-primary\"\n          >\n            Submit\n          </button>\n        </div>\n      </div>\n    </form>\n  </div>\n</template>\n\n<script>\nimport $ from 'jquery'\n\nexport default {\n  data () {\n    return {\n      title: 'Submit Select',\n      desc: 'Submit select with <code>name</code> attribute in form.'\n    }\n  },\n  methods: {\n    submit () {\n      alert($('form').serialize())\n      return false\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"