export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          :options=\"options\"\n        >\n          <option value=\"1\">\n            \n          </option>\n          <option value=\"2\">\n            \n          </option>\n          <option value=\"3\">\n            \n          </option>\n          <option value=\"4\">\n            \n          </option>\n          <option value=\"5\">\n            \n          </option>\n          <option value=\"6\">\n            \n          </option>\n          <option value=\"7\">\n            \n          </option>\n          <option value=\"8\">\n            \n          </option>\n          <option value=\"9\">\n            \n          </option>\n          <option value=\"10\">\n            \n          </option>\n          <option value=\"11\">\n            \n          </option>\n          <option value=\"12\">\n            \n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'Basic Select',\n      desc: 'Custom i18n options.',\n      options: {\n        filter: true,\n        formatSelectAll () {\n          return '[]'\n        },\n        formatAllSelected () {\n          return ''\n        },\n        formatCountSelected (count, total) {\n          return ' ' + total + '  ' + count + ' '\n        },\n        formatNoMatchesFound () {\n          return ''\n        }\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"