export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Group Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          :options=\"options\"\n        >\n          <optgroup label=\"Group 1\">\n            <option value=\"1\">\n              1\n            </option>\n            <option value=\"2\">\n              2\n            </option>\n            <option value=\"3\">\n              3\n            </option>\n            <option value=\"4\">\n              4\n            </option>\n            <option value=\"5\">\n              5\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 2\">\n            <option value=\"6\">\n              6\n            </option>\n            <option value=\"7\">\n              7\n            </option>\n            <option value=\"8\">\n              8\n            </option>\n            <option value=\"9\">\n              9\n            </option>\n            <option value=\"10\">\n              10\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 3\">\n            <option value=\"11\">\n              11\n            </option>\n            <option value=\"12\">\n              12\n            </option>\n            <option value=\"13\">\n              13\n            </option>\n            <option value=\"14\">\n              14\n            </option>\n            <option value=\"15\">\n              15\n            </option>\n          </optgroup>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'Hide Optgroup Checkboxes',\n      desc: 'Use <code>hideOptgroupCheckboxes: true</code> option to hide the optgroup checkboxes.',\n      options: {\n        multiple: true,\n        hideOptgroupCheckboxes: true,\n        multipleWidth: 60\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\nse