export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        No container\n      </label>\n\n      <div class=\"col-sm-10\">\n        <div class=\"parent\">\n          <MultipleSelect>\n            <option value=\"1\">\n              January\n            </option>\n            <option value=\"2\">\n              February\n            </option>\n            <option value=\"3\">\n              March\n            </option>\n            <option value=\"4\">\n              April\n            </option>\n            <option value=\"5\">\n              May\n            </option>\n            <option value=\"6\">\n              June\n            </option>\n            <option value=\"7\">\n              July\n            </option>\n            <option value=\"8\">\n              August\n            </option>\n            <option value=\"9\">\n              September\n            </option>\n            <option value=\"10\">\n              October\n            </option>\n            <option value=\"11\">\n              November\n            </option>\n            <option value=\"12\">\n              December\n            </option>\n          </MultipleSelect>\n        </div>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        With container\n      </label>\n\n      <div id=\"container\" class=\"col-sm-10\">\n        <div class=\"parent\">\n          <MultipleSelect\n            :options=\"options\"\n          >\n            <option value=\"1\">\n              January\n            </option>\n            <option value=\"2\">\n              February\n            </option>\n            <option value=\"3\">\n              March\n            </option>\n            <option value=\"4\">\n              April\n            </option>\n            <option value=\"5\">\n              May\n            </option>\n            <option value=\"6\">\n              June\n            </option>\n            <option value=\"7\">\n              July\n            </option>\n            <option value=\"8\">\n              August\n            </option>\n            <option value=\"9\">\n              September\n            </option>\n            <option value=\"10\">\n              October\n            </option>\n            <option value=\"11\">\n              November\n            </option>\n            <option value=\"12\">\n              December\n            </option>\n          </MultipleSelect>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The Container',\n      desc: 'Use <code>container</code> option to solve the cut off problem for example parent node is <code>overflow: hidden</code>.',\n      options: {\n        container: '#container'\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\n.parent {\n  overflow: hidden;\n}\nselect {\n  width: 100%;\n}\n</style>\n"