export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Methods\n      </label>\n\n      <div class=\"col-sm-10\">\n        <button\n          class=\"btn btn-secondary\"\n          @click=\"refreshOptions\"\n        >\n          refreshOptions\n        </button>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          ref=\"select\"\n          :options=\"options\"\n        >\n          <option value=\"text1\">\n            text1\n          </option>\n          <option value=\"text2\">\n            text2\n          </option>\n          <option value=\"text3\">\n            text3\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'The refreshOptions',\n      desc: 'Use <code>multipleSelect(\\'refreshOptions\\', {})</code> to refresh the options object.',\n      options: {\n        filter: true\n      }\n    }\n  },\n  methods: {\n    refreshOptions () {\n      this.$refs.select.refreshOptions({\n        filter: false\n      })\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"