export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          :data=\"data\"\n          :options=\"options\"\n        />\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    const data = []\n    for (let i = 0; i < 2000; i++) {\n      data.push(i)\n    }\n    return {\n      title: 'Large Select',\n      desc: 'Large select with filter.',\n      data,\n      options: {\n        filter: true\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"