export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Hide Select All\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect\n          :options=\"options\"\n        >\n          <option value=\"1\">\n            First\n          </option>\n          <option value=\"2\">\n            Second\n          </option>\n          <option value=\"3\">\n            Third\n          </option>\n          <option value=\"4\">\n            Fourth\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'Hide Select All',\n      desc: 'Use <code>selectAll: false</code> to hide select all.',\n      options: {\n        selectAll: false\n      }\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"