export default "<template>\n  <div>\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Basic Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect>\n          <option\n            value=\"1\"\n            selected=\"selected\"\n          >\n            January\n          </option>\n          <option\n            value=\"2\"\n            disabled=\"disabled\"\n          >\n            February\n          </option>\n          <option\n            value=\"3\"\n            selected=\"selected\"\n            disabled=\"disabled\"\n          >\n            March\n          </option>\n          <option value=\"4\">\n            April\n          </option>\n          <option value=\"5\">\n            May\n          </option>\n          <option value=\"6\">\n            June\n          </option>\n          <option value=\"7\">\n            July\n          </option>\n          <option value=\"8\">\n            August\n          </option>\n          <option value=\"9\">\n            September\n          </option>\n          <option value=\"10\">\n            October\n          </option>\n          <option value=\"11\">\n            November\n          </option>\n          <option value=\"12\">\n            December\n          </option>\n        </MultipleSelect>\n      </div>\n    </div>\n\n    <div class=\"form-group row\">\n      <label class=\"col-sm-2\">\n        Group Select\n      </label>\n\n      <div class=\"col-sm-10\">\n        <MultipleSelect>\n          <optgroup\n            label=\"Group 1\"\n            disabled=\"disabled\"\n          >\n            <option\n              value=\"1\"\n              selected\n            >\n              Option 1\n            </option>\n            <option value=\"2\">\n              Option 2\n            </option>\n            <option value=\"3\">\n              Option 3\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 2\">\n            <option value=\"4\">\n              Option 4\n            </option>\n            <option\n              value=\"5\"\n              selected\n            >\n              Option 5\n            </option>\n            <option value=\"6\">\n              Option 6\n            </option>\n          </optgroup>\n          <optgroup label=\"Group 3\">\n            <option\n              value=\"7\"\n              disabled=\"disabled\"\n            >\n              Option 7\n            </option>\n            <option value=\"8\">\n              Option 8\n            </option>\n            <option value=\"9\">\n              Option 9\n            </option>\n          </optgroup>\n        </MultipleSelect>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nexport default {\n  data () {\n    return {\n      title: 'Selected/Disabled Options',\n      desc: 'Selected or Disabled options.'\n    }\n  }\n}\n</script>\n\n<style scoped>\nselect {\n  width: 100%;\n}\n</style>\n"